/* STYLE THE HTML ELEMENTS (INCLUDES RESETS FOR THE DEFAULT FIELDSET AND LEGEND STYLES) */
body,
html {
  margin: 0;
  padding: 0.5rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-family: "Open Sans", "Helvetica", sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #b3d2ce; }

fieldset {
  margin: 0;
  padding: 0.3rem;
  box-sizing: border-box;
  display: block;
  border: none;
  border: solid 1px #CCC;
  min-width: 0;
  background-color: #b3d2ce; }

body:not(:-moz-handler-blocked) fieldset {
  display: table-cell; }

/* TOGGLE STYLING */
.btnToggle {
  margin: 0 0 0rem;
  box-sizing: border-box;
  font-size: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: stretch; }
  .btnToggle input {
    width: 0;
    height: 0;
    position: absolute;
    left: -9999px; }
  .btnToggle input + label {
    margin: 0;
    padding: .75rem 2rem;
    box-sizing: border-box;
    position: relative;
    display: inline-block;
    border: solid 1px #DDD;
    background-color: #FFF;
    font-size: 1rem;
    line-height: 70%;
    font-weight: 600;
    text-align: center;
    box-shadow: 0 0 0 rgba(255, 255, 255, 0);
    transition: border-color .15s ease-out, color .25s ease-out, background-color .15s ease-out, box-shadow .15s ease-out;
    /* ADD THESE PROPERTIES TO SWITCH FROM AUTO WIDTH TO FULL WIDTH */
    /*flex: 0 0 50%; display: flex; justify-content: center; align-items: center;*/
    /* ----- */ }
    .btnToggle input + label:first-of-type {
      border-radius: 6px 0 0 6px;
      border-right: none; }
    .btnToggle input + label:last-of-type {
      border-radius: 0 6px 6px 0;
      border-left: none; }
  .btnToggle input:hover + label {
    border-color: #213140; }
  .btnToggle input:checked + label {
    background-color: #185EAA;
    color: #FFF;
    box-shadow: 0 0 10px rgba(102, 179, 251, 0.5);
    border-color: #185EAA;
    z-index: 1; }
  .btnToggle input:focus + label {
    outline: dotted 1px #CCC;
    outline-offset: .45rem; }
  @media (max-width: 800px) {
    .btnToggle input + label {
      padding: .55rem .25rem;
      flex: 0 0 50%;
      display: flex;
      justify-content: center;
      align-items: center; } }

